import React, { useState, useContext, useEffect } from 'react'
import DataContext from '../../context/context'
import chunk from 'lodash/chunk'
import { Grid } from '@material-ui/core'
import { generateSeats, fillBackground , drawRow} from './utils'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  container: {
    width: '100%'
  },
  avatar: {
    marginTop: 16
  }
}));

const Room = ({ match }) => {
  const { rooms } = useContext(DataContext)
  const [canvas, setCanvas] = useState(null)
  const [seats, setSeats] = useState([])

  const classes = useStyles()

  useEffect(() => {
    setSeats(generateSeats(rooms[match.params.id]))
  }, [match.params.id, rooms])

  useEffect(() => {
    setCanvas(document.getElementById('canvas').getContext('2d'))
  }, []);

  useEffect(() => {
    if (canvas) {
      fillBackground(canvas)
      const { overallPlaces, overallRows } = rooms[match.params.id]
      const chunks = chunk(seats, overallPlaces / overallRows)
      chunks.forEach((ch, index) => drawRow(canvas, ch, index))
    }
   
  }, [canvas, match.params.id, rooms, seats])

  return (
    <>
      <Grid
        container
        justify='center'
        className={classes.container}
      >
        <Grid item>
          <canvas
            id='canvas'
            width='410'
            height='360'
            style={{ marginTop: 16 }}
          />
        </Grid>
      </Grid>

    </>
  );
};

export default Room;