import React, { useState } from 'react'
import Grid from '@material-ui/core/Grid'
import {
  BrowserRouter as Router,
  Route,
} from "react-router-dom";
import Header from './Header'
import SideBar from './SideBar'
import paths from '../../static/paths'
import Repertory from '../Repertory/Repertory'
import Welcome from '../Welcome/Welcome'
import Rooms from '../Rooms/Rooms'
import Room from '../Rooms/Room'
import Register from '../Register/Register'

const Main = () => {
  const [toggle, setToggle] = useState(false)

  return (
    <Grid container
      direction='column'
    >
      <Router>
        <Header toggle={toggle} handleToggle={setToggle} />
        <SideBar toggle={toggle} handleToggle={setToggle} />
        <Grid item>
          <Route exact path={paths.default}component={Welcome} />
          <Route path={paths.repertory}component={Repertory} />
          <Route exact path={paths.rooms} component={Rooms} />
          <Route path={paths.room} component={Room} />
          <Route path={paths.register} component={Register}/>
        </Grid>
      </Router>
    </Grid>
  )
}

export default Main