const POS_X = 10
const POS_Y = 50

export const generateSeats = ({ overallPlaces, overallRows }) => {
  const arr = []
  const totalSeatsInRow = Math.floor(overallPlaces / overallRows)
  for (let i = 0; i < overallRows; i++) {
    for (let seatNumber = 0; seatNumber < totalSeatsInRow; seatNumber++) {
      arr.push(`${String.fromCharCode(65 + i)}${seatNumber}`)
    }
  }
  return arr
}

export const fillBackground = canvas => {
  canvas.fillStyle = '#f0f0f0'
  canvas.fillRect(0, 0, 410, 360)

  // screen
  canvas.fillStyle = '#0f0f0f'
  canvas.fillRect(50, 0, 320, 20)

  //stairs
  canvas.fillStyle = '#0f0f0f'
  canvas.fillRect(350, 90, 410, 140)
}

export const drawRow = (canvas, chunk, rowNuber) => {
  chunk.map((item, index) => drawItem(canvas, item, POS_X + 30 * index , POS_Y + 30 * rowNuber))
}

const drawItem = (canvas , item, posX, posY) => {

  const width = 20
  const height = 25

  canvas.fillStyle = '#fff' 
  canvas.fillRect( posX, posY, width, height)
  drawText(canvas, item, posX, posY)
}


const drawText = (canvas , item, posX, posY) => {
    canvas.fillStyle = '#000'
    canvas.font='12px'
    canvas.textAlign = 'center'
    canvas.fillText( item, posX + 10,  posY + 15)
}